











































































































import { Vue, Prop, Component, Watch } from 'vue-property-decorator'
import { lab } from 'color-convert/conversions'

@Component
export default class EventShowCard extends Vue {
    @Prop(Object)
    item?: Connex.Thor.Event
    @Prop(Array)
    params?: ABI.EventInputItem[]

    private tabIndex = 0
    private data?: any[]

    private columns = [
        {
            label: 'ID',
            field: 'index'
        },
        {
            label: 'Name',
            field: 'name'
        },
        {
            label: 'Type',
            field: 'type'
        },
        {
            label: 'Data',
            field: 'value'
        }
    ]

    @Watch('item')
    @Watch('params')
    onItemChange() {
        this.data = this.params!.map((item, i) => {
            return {
                id: i,
                name: item.name,
                type: item.type,
                indexed: item.indexed,
                value: (this.item!.decoded as any)[item.name]
            }
        })
    }

    created() {
        this.onItemChange()
    }
}
